header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: #333;
    color: #fff;
    position: sticky;
    z-index: 5;
}

.logo {
    font-size: 1.5rem;
    font-weight: bold;
}

@media only screen and (max-width: 1000px) {
    .logo {
        display: none;
    }
}

nav ul {
    list-style: none;
    display: flex;
    gap: 20px;
}

nav ul li {
    margin: 0;
}

nav ul li a {
    text-decoration: none;
    color: #fff;
    font-weight: bold;
    font-size: 1rem;
    transition: color 0.3s ease;
}

nav ul li a:hover {
    color: #ffd700;
    /* Change color on hover */
}

button {
    background-color: #333;
    color: #fff;
    border: none;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease, color 0.3s ease;
    transition: 0.5s;
}

button:hover {
    background-color: #333;
    color: #fff;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.9);
    transform: scale(1.2);
}

.active {
    color: #ffd700;
    font-weight: bold;
}

.buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 1em;
}

@media only screen and (max-width: 768px) {
    header {
        flex-direction: column;
        align-items: center;
    }
    nav {
        margin-top: 10px;
        /* Adjust spacing between logo and nav links */
    }
    ul {
        padding: 0;
        text-align: center;
    }
    ul li {
        display: block;
        margin-bottom: 10px;
        /* Adjust spacing between nav links */
    }
    .buttons {
        margin-top: 10px;
        /* Adjust spacing between nav links and buttons */
    }
}